.draggable-area {
  top: 0;
  width: 100%;
  height: 34px;
  position: absolute;
  -webkit-app-region: drag;
  z-index: -1;
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

body, iframe {
  margin: 0;
  padding: 0;
  border: 0;
  background-color: black;
  color: white;
  font-family: Arial, serif;
}

body {
  overflow: hidden;
}

.chrome-tabs {
  height: 34px;
  padding: 0 0 4px 4px;
}

.tabs-plus-button {
  font-size: 20px;
  line-height: 28px;
  width: 28px;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  transition: all 200ms;
  border-radius: 14px;
}

.tabs-plus-button:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

.tabs-plus-button, .chrome-tab {
  -webkit-app-region: no-drag;
}

.tabs-container {
  display: flex;
  background-color: #292a2d;
}

.chrome-tabs {
  background-color: #292a2d;
}

.chrome-tabs .chrome-tab-title {
  color: white !important;
}

.chrome-tabs .chrome-tab[active] .chrome-tab-content {
  background-color: #333333;
}

.chrome-tabs .chrome-tab[active] .chrome-tab-background > svg .chrome-tab-geometry {
  fill: #333333;
}

.chrome-tabs .chrome-tab .chrome-tab-background > svg .chrome-tab-geometry {
  fill: #222222;
}

.chrome-tabs .chrome-tab .chrome-tab-close:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.chrome-tabs .chrome-tab .chrome-tab-close {
  background-image: url('data:image/svg+xml;utf8,<svg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 8 8%27><path stroke=%27rgba%28255, 255, 255, 1%29%27 stroke-linecap=%27square%27 stroke-width=%271.5%27 d=%27M0 0 L8 8 M8 0 L0 8%27></path></svg>');
}
