body {
  background-color: #333333; /* Set your desired background color */
  margin: 0; /* Remove default margin to ensure full coverage */
  padding: 0; /* Remove default padding */
}

.main-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.error-message {
  background-color: rgba(255, 0, 0, 0.5); /* Red with opacity */
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 20px; /* Center the error message horizontally and remove bottom margin */
  text-align: center; /* Center the text */
}

.main-buttons {
  display: flex;
  justify-content: space-around; /* Adjust this to space evenly or as needed */
}

.button {
  border: 2px solid #fff; /* White stroke */
  background-color: #2c3e50; /* Dark background color, adjust as needed */
  color: #fff; /* Text color */
  padding: 10px 20px; /* Adjust as needed for button size */
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px; /* Add margin between buttons */
  transition: background-color 0.3s ease, color 0.3s ease;
}

.button:hover {
  background-color: #34495e; /* Darker background color on hover, adjust as needed */
  color: #fff; /* Text color on hover */
}

.back-button {
  margin: 5px; /* Add margin to match other buttons */
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  background-color: #2c3e50; /* Dark background color, adjust as needed */
  color: #fff; /* Text color */
  border: 2px solid #fff; /* White stroke */
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.back-button:hover {
  background-color: #34495e; /* Darker background color on hover, adjust as needed */
}

.copy-button {
  background-color: #4caf50; /* Green */
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.copy-button:hover {
  background-color: #45a049;
}

.faq-page-container {
  display: flex;
  flex-direction: column;
}

.faq-page-content {
  overflow-y: scroll;
  height: calc(100vh - 137px);
  padding: 20px;
}

.faq-page-content-text {
  max-width: 800px;
  margin: auto;
}
